import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { FormGroup, FormControl, Validators } from '@angular/forms';
import {ProductService} from '../product.service';
import { v1 as uuidv1} from 'uuid';

@Component({
  selector: 'app-add-or-view-product',
  templateUrl: './add-or-view-product.component.html',
  styleUrls: ['./add-or-view-product.component.less']
})
export class AddOrViewProductComponent implements OnInit {

  productId = null;
  productData = null;
  successMsg = '';
  newId = uuidv1();
  form;

  constructor(private productService: ProductService, private currRoute: ActivatedRoute,
    private router: Router) { }

  ngOnInit(): void {
    this.productId = this.currRoute.snapshot.paramMap.get('id');
    this.initForm();
    
    if (this.productId) {
      this.getProduct(this.productId)
    } else {
      this.form.patchValue({
        "id" : this.newId,
        "viewTimes" : 0
      });
    }
  }

  getProduct(id: number): void {
    this.productService.getIndividualProduct(id).subscribe((resData) => {
      resData.viewTimes += 1;
      this.productService.updateProduct(resData).subscribe((res) => {
        if (res) {
          this.productData = res;
          this.form.setValue(this.productData);
        }
      });
    });
  }

  initEditForm(): void {

    let data = this.productData; 
    this.form = new FormGroup({
      "id" : new FormControl(data.id, Validators.required),
      "productName" : new FormControl(data.productName, Validators.required),
      "productDescription" : new FormControl(data.productDescription, Validators.required),
      "manufacturer" : new FormControl(data.manufacturer, Validators.required),
      "price" : new FormControl(data.price, Validators.required),
      "quantity" : new FormControl(data.quantity, Validators.required),
      "viewTimes" : new FormControl(data.viewTimes, Validators.required)
    });
  }

  initForm(): void {
    this.form = new FormGroup({
      "id" : new FormControl('', Validators.required),
      "productName" : new FormControl('', Validators.required),
      "productDescription" : new FormControl('', Validators.required),
      "manufacturer" : new FormControl('', Validators.required),
      "price" : new FormControl('', Validators.required),
      "quantity" : new FormControl('', Validators.required),
      "viewTimes" : new FormControl('', Validators.required) 
    });
  }

  submit(): void {
    if (this.form.status === 'VALID') {
      if (this.productId) {
        this.update();
      } else {
        this.create();
      }
    }
  }

  update(): void {
    this.productService.updateProduct(this.form.value).subscribe((resData) => {
      if (resData) {
        this.successMsg = "Details Updated";
      }
    });
  }

  create(): void {
    this.productService.addProduct(this.form.value).subscribe((resData) => {
      if (resData) {
        this.successMsg = "Product Added";
        this.productId = this.newId;
      }
    });
  }

  back(): void {
    this.router.navigate(['/products/productsummary']);
  }

  canDeactivate(): boolean {
    if (!this.form.pristine || this.form.touched) {
      return window.confirm('Continuing will discard the changes. Like to Proceed ? ');
    } else {
      return true;
    }
  }
}
