import { Component, OnInit } from '@angular/core';
import { ChartType, ChartOptions } from 'chart.js';
import { Label } from 'ng2-charts';
import * as pluginDataLabels from 'chartjs-plugin-datalabels';
import {ProductService} from '../product.service';

@Component({
  selector: 'app-top-viewed-products',
  templateUrl: './top-viewed-products.component.html',
  styleUrls: ['./top-viewed-products.component.less']
})
export class TopViewedProductsComponent implements OnInit {

  public pieChartOptions: ChartOptions = {
    responsive: true,
    legend: {
      position: 'top',
    },
    plugins: {
      datalabels: {
        formatter: (value, ctx) => {
          const label = ctx.chart.data.labels[ctx.dataIndex];
          return label;
        },
      },
    }
  };
  public pieChartLabels: Label[] = [];
  public pieChartData: number[] = [];
  public pieChartType: ChartType = 'pie';
  public pieChartLegend = true;
  public pieChartPlugins = [pluginDataLabels];
  public pieChartColors = [
    {
      backgroundColor: ['rgba(255,0,0,0.3)', 'rgba(0,255,0,0.3)', 'rgba(0,0,255,0.3)',
      'rgba(0,255,255,0.3)', 'rgba(255,255,0,0.3)', 'rgba(0,255,255,0.3)', 'rgba(255,0,255,0.3)',
      'rgba(255,0,0,0.3)','rgba(255,0,0,0.3)','rgba(255,0,0,0.3)','rgba(255,0,0,0.3)'],
    },
  ];

  noOfTop = 3;
  constructor(private pdtService: ProductService) { }

  ngOnInit(): void {
    this.getTopViewedPdts();
  }

  getTopViewedPdts(): void {
    this.pdtService.getProductsWithParam({limit: this.noOfTop}).subscribe((data) => {
      for(let i=0; i< data.length ; i++) {
        this.pieChartLabels.push(data[i].productName);
        this.pieChartData.push(data[i].viewTimes);
      }
    });
  }

  handleChange(e): void {
      this.noOfTop = e.target.value;
      this.pieChartLabels = [];
      this.pieChartData = [];
      this.getTopViewedPdts();
  }

}
