import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginComponent } from '../login/login.component';
import { SignupComponent } from '../signup/signup.component';
import { AboutComponent } from '../about/about.component';
import { ViewRegistrationDetailsComponent } from '../view-registration-details/view-registration-details.component';
import { AuthenticateGuard } from './authenticate.guard';
import {CanDeactivateGuard} from './deactivate.guard';
import {ProductsModule} from '../products/products.module';
import { ProductsummaryComponent } from '../products/productsummary/productsummary.component';
import { AddOrViewProductComponent } from '../products/add-or-view-product/add-or-view-product.component';


const routes: Routes = [
  {
    'path' : 'login',
    'component' : LoginComponent
  },
  {
    'path' : 'signup',
    'component' : SignupComponent
  },
  {
    'path' : 'registrationdetails',
    'component' : ViewRegistrationDetailsComponent,
    'canActivate' : [AuthenticateGuard],
    'canDeactivate' : [CanDeactivateGuard]
  },
  {
    'path' : 'products/productsummary',
    'component' : ProductsummaryComponent
},
{
    'path' : 'products/productdetail',
    'component' : AddOrViewProductComponent,
    'canActivate' : [AuthenticateGuard],
    'canDeactivate' : [CanDeactivateGuard]
},
{
    'path' : 'products/productdetail/:id',
    'component' : AddOrViewProductComponent,
    'canActivate' : [AuthenticateGuard],
    'canDeactivate' : [CanDeactivateGuard]
},
{
  'path' : '',
  'redirectTo' : '/products/productsummary',
  'pathMatch' : 'full'
},
{
  'path' : '**',
  'redirectTo' : '/products/productsummary'
},
];


@NgModule({
  declarations: [],
  imports: [
    RouterModule.forRoot(routes, {useHash: true})
  ],
  exports: [
    RouterModule
  ]
})
export class RoutingModule { }
