import { Component, OnInit } from '@angular/core';
import { ProductService } from '../product.service';
import { Product } from '../product';
import {UserAuthServiceService} from '../../user-auth-service.service';
import {Router} from '@angular/router';
import { FormControl } from '@angular/forms';

@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.less']
})
export class ProductListComponent implements OnInit {

  productsArr: Product[];

  productName = true;
  productDescription = true;
  manufacturer = true;
  price = true;
  quantity = true;
  deleteList = [];


  constructor(private pdtService: ProductService,
    private auth: UserAuthServiceService,
    private router: Router) {
   }

  ngOnInit(): void {
    this.getAllProducts();
  }


  getAllProducts(): void {
    this.pdtService.getProducts().subscribe((resData: Product[]) => {
      this.productsArr = resData;
    });
  }


  handleClick(e): void {
    let target = e.target;
    if (target.type === 'checkbox') {
      this[target.value] = target.checked;
    }
  }

  delete(e): void {
    e.preventDefault();

    if (this.auth.activeUserData) {
      if (this.deleteList.length) {
        this.pdtService.deleteProduct(this.deleteList.join()).subscribe((res) => {
          this.getAllProducts();
        });
      }
    } else {
      this.router.navigate(['/login']);
    }
  }

  addToDelete(e, pdtId) {
    let value = e.target.checked;

    if (value) {
      this.deleteList.push(pdtId);
    } else {
      this.deleteList.splice(this.deleteList.indexOf(pdtId), 1);
    }
  }

  handleSearch(val: string) {
    this.pdtService.getProductsWithParam({q: val}).subscribe((data) => {
      this.productsArr = data;
    });
  }
}
